* {
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  color: #000;
  margin: 0;
  padding: 0;
  font-size: 16px;
}

h1,
h2 {
  font-family: "Bebas Neue", sans-serif;
  font-size: 50px;
  color: #132F57;
}
@media screen and (max-width: 1200px) {
  h1,
h2 {
    font-size: 30px;
  }
}

h1.sea,
h2.sea,
h3.sea,
h4.sea,
h5.sea,
a.sea,
p.sea,
b.sea,
i.sea,
span.sea {
  color: #13b7aa;
}
h1.sky,
h2.sky,
h3.sky,
h4.sky,
h5.sky,
a.sky,
p.sky,
b.sky,
i.sky,
span.sky {
  color: #3C9CD7;
}

h3,
h4,
h5,
ul,
li,
p {
  line-height: 35px;
  margin: 0;
}
@media screen and (max-width: 1200px) {
  h3,
h4,
h5,
ul,
li,
p {
    line-height: 25px;
  }
}

section {
  overflow: hidden;
}

b {
  color: #132F57;
}

a {
  text-decoration: none;
  color: #13b7aa;
}
a:hover {
  text-decoration: underline;
}

input {
  border: 1px solid #e0e0e0;
  padding: 20px 40px;
  font-size: 16px;
  border-radius: 5px;
}

input[type=submit],
input[type=button],
.btn {
  display: inline-block;
  text-decoration: none;
  border: 1px solid #132F57;
  text-align: center;
  border-radius: 5px;
  padding: 21px 69px;
  background: #132F57;
  color: #fff;
  cursor: pointer;
  transition: 0.5s ease-out;
}
input[type=submit]:hover,
input[type=button]:hover,
.btn:hover {
  box-shadow: rgba(0, 0, 0, 0.1) 9px 6px 20px;
  text-decoration: none;
}
input[type=submit].capsule,
input[type=button].capsule,
.btn.capsule {
  border-radius: 30px;
}
input[type=submit].arrow,
input[type=button].arrow,
.btn.arrow {
  position: relative;
  padding-right: 120px;
}
@media screen and (max-width: 1200px) {
  input[type=submit].arrow,
input[type=button].arrow,
.btn.arrow {
    padding-right: 56px;
  }
}
input[type=submit].arrow::before,
input[type=button].arrow::before,
.btn.arrow::before {
  content: "";
  width: 20px;
  height: 0px;
  position: absolute;
  border-bottom: 1px solid;
  right: 69px;
  top: 50%;
}
@media screen and (max-width: 1200px) {
  input[type=submit].arrow::before,
input[type=button].arrow::before,
.btn.arrow::before {
    right: 22px;
  }
}
input[type=submit].arrow::after,
input[type=button].arrow::after,
.btn.arrow::after {
  content: "";
  width: 6px;
  height: 6px;
  position: absolute;
  right: 69px;
  top: calc(50% - 3px);
  border-top: 1px solid;
  border-right: 1px solid;
  transform: rotate(45deg);
}
@media screen and (max-width: 1200px) {
  input[type=submit].arrow::after,
input[type=button].arrow::after,
.btn.arrow::after {
    right: 22px;
  }
}

@media screen and (max-width: 1200px) {
  .btn {
    padding: 14px 25px;
  }
}

@media screen and (max-width: 1200px) {
  input[type=button],
input[type=submit] {
    padding: 21px 69px;
  }
}

.sea input[type=button]:hover,
.sea .btn:hover {
  background-color: #13b7aa;
  border-color: #13b7aa;
}

.sky input[type=button]:hover,
.sky .btn:hover {
  background-color: #3C9CD7;
  border-color: #3C9CD7;
}

.ico {
  position: relative;
}
.ico::before {
  content: "";
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
}
.ico.size-s {
  margin-left: 25px;
}
.ico.size-s::before {
  left: -25px;
  width: 20px;
  height: 20px;
  top: 8px;
}
.ico.point::before {
  background-image: url(../img/point.png);
  background-image: url(../img/svg/point.svg);
}

