.header {
  font-size: 14px;
}
@media screen and (max-width: 1200px) {
  .header {
    font-family: "Bebas Neue", sans-serif;
    font-size: 30px;
  }
}
.header.light {
  color: #fff;
}
.header.light a {
  color: #fff;
}
.header.dark {
  color: #132F57;
}
@media screen and (max-width: 1200px) {
  .header.dark {
    color: #fff;
  }
}
.header.dark path {
  stroke: #132F57;
}
.header.dark a {
  color: #132F57;
}
@media screen and (max-width: 1200px) {
  .header.dark a {
    color: #fff;
  }
}
.header.dark .active svg path {
  stroke: #fff;
}
.header.atop {
  min-height: 130px;
  margin-bottom: -180px;
  position: relative;
  z-index: 200;
}
@media screen and (max-width: 1200px) {
  .header.atop {
    position: absolute;
    width: 100%;
  }
}
.header__nav {
  display: flex;
  align-items: center;
  padding-top: 72px;
  padding-bottom: 50px;
}
@media screen and (max-width: 1200px) {
  .header__nav {
    padding-top: 50px;
  }
}
.header__nav-box {
  display: flex;
  padding-left: 0;
}
.header__nav-box-logo img {
  display: none;
}
@media screen and (max-width: 1200px) {
  .header__nav-box-logo img {
    display: block;
    position: absolute;
    top: 60px;
    height: 80px;
  }
}
@media screen and (max-width: 1200px) {
  .header__nav-box-company {
    display: none;
  }
}
.header__nav-box-contacts {
  display: none;
}
@media screen and (max-width: 1200px) {
  .header__nav-box-contacts {
    display: block;
    font-family: "Montserrat", sans-serif;
    position: absolute;
    bottom: 20px;
    margin: 0;
    padding: 0;
  }
  .header__nav-box-contacts li {
    font-size: 13px;
    padding: 0;
  }
  .header__nav-box-contacts li a {
    border-bottom: 2px dotted #13b7aa;
  }
}
@media screen and (max-width: 1200px) {
  .header__nav-box {
    position: fixed;
    z-index: 100;
    width: 100%;
    height: 100%;
    background-color: #132F57;
    left: 0;
    top: -100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: 1s;
    opacity: 0;
    padding: 20px;
  }
  .header__nav-box li {
    padding: 5px;
  }
}
.header__nav li {
  display: inline-block;
  padding-right: 45px;
}
@media screen and (max-width: 1200px) {
  .header__nav li {
    padding-right: 0px;
  }
}
.header__nav a {
  text-decoration: none;
}
.header__nav a:hover {
  text-decoration: underline;
}
.header__nav-logo img {
  height: 55px;
  transition: 0.5s ease-out;
}
@media screen and (max-width: 1200px) {
  .header__nav-logo img {
    height: 90px;
  }
}
.header__nav-logo img:hover {
  transform: scale(1.05);
}
.header .nav__ico {
  display: none;
}
.header .nav__ico.dark path {
  stroke: #132F57;
}
.header .nav__ico.dark.active path {
  stroke: #fff !important;
}
@media screen and (max-width: 1200px) {
  .header .nav__ico {
    cursor: pointer;
    display: flex;
    z-index: 1000;
    position: fixed;
    right: 0;
    top: 15px;
  }
}
.header .active ~ ul.header__nav-box {
  top: 0;
  opacity: 1;
}
.header svg {
  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1);
}
.header .active svg {
  transform: rotate(90deg);
}
.header path {
  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1), stroke-dasharray 500ms cubic-bezier(0.4, 0, 0.2, 1), stroke-dashoffset 500ms cubic-bezier(0.4, 0, 0.2, 1);
  stroke: #fff;
}
.header path:nth-child(1) {
  transform-origin: 36% 40%;
}
.header path:nth-child(2) {
  stroke-dasharray: 29 299;
}
.header path:nth-child(3) {
  transform-origin: 35% 63%;
}
